* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  overflow-x: hidden;
  font-family: 'Gambetta', serif;
  background-color: black;
}

.mask-container {
 opacity:1;
  z-index: 99999;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  mix-blend-mode: difference;
  

}

#mask {
  height: 60px;
  width: auto;
  filter: blur(0px);
}

#name {
  transition: transform 0.2s;
  text-decoration-line: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 5px;
}

#name:hover {
  opacity:1;
  cursor: pointer;
}

#name:active {
  transform: scale(0.95) !important;
}

#gidd {
  display: none !important;
  position: fixed;
  top: 20px;
  left: 31px;
  color: white;
  z-index: 999999;
  font-size: 50px;
}

#gidd div{
  display: inline;
  float: left;
  opacity:0.5;
}


#arrow {
  position: fixed;
  transform-origin: center;
}


#me {
 display: none !important;
  z-index: 999999;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 12px;
  position: fixed;
  bottom: 20px;
  left: 20px;
  background-color: rgba(0, 0, 0, 1);
  padding: 3px 11px;
  border-radius: 100px;
  transition: transform 0.2s;
  opacity: 0.5;
  color: rgba(255, 255, 255, 1);
  text-decoration: none;
}

#me:hover {
  opacity: 1;
}

#me:active {
  transform: scale(0.95);
}

.webgl {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

#blur-square {
  z-index: 100;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  backdrop-filter: blur(12px);

  -webkit-backdrop-filter: blur(12px);
}

#square-color {
  z-index: 999999;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background-color: rgba(233, 119, 163, 0.3);
  mix-blend-mode: soft-light;
}

#controls {
  position: fixed;
  top: 10px;
  right: 10px;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 10px;
  border-radius: 8px;
  width: 200px;
  z-index: 9999;
}

.slider-container {
  margin-bottom: 10px;
}

label {
  font-weight: bold;
}

input[type="range"] {
  -webkit-appearance: none;
  /* Override default CSS styles */
  appearance: none;
  width: 100%;
  /* Specific width if necessary */
  height: 8px;
  /* Set the slider thumb height */
  background: red;
  /* Red track color */
  outline: none;
  /* Remove outline */
  opacity: 0.7;
  /* Make the slider slightly transparent */
  transition: opacity .2s;
}

input[type="range"]:hover {
  opacity: 1;
  /* Fully opaque on hover */
}

/* Styling the thumb */
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  /* Override default look */
  appearance: none;
  width: 20px;
  /* Set a specific width */
  height: 20px;
  /* Set a specific height */
  background: #ffffff;
  /* White thumb */
  cursor: pointer;
  /* Cursor on hover */
  border: 2px solid red;
  /* Red border */
}

input[type="range"]::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: #ffffff;
  cursor: pointer;
  border: 2px solid red;
}

input[type="range"]::-ms-thumb {
  width: 20px;
  height: 20px;
  background: #ffffff;
  cursor: pointer;
  border: 2px solid red;
}

/* Styling the track for Mozilla Firefox */
input[type="range"]::-moz-range-track {
  background: red;
  height: 8px;
}

/* Styling the track for Internet Explorer */
input[type="range"]::-ms-track {
  background: red;
  height: 8px;
  border-color: transparent;
  /* Removes the additional border */
}

/* Remove the focus outline as it might look off on some browsers */
input[type="range"]:focus {
  outline: none;
}